import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

import "./foodpage.css";

class Food extends Component{
    render() {
        return(
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className='about-grid'>
                    <Cell col={12}>
                        <div className = 'food-text'>
                            <h1>Foods Blog</h1> 
                         
                        </div>
                    </Cell>

                    <Cell col={8}>
                        <div className = 'food-paragraph'>
                            <h1>First Impression: In-N-Out</h1>
                            <p>In-N-Out is the pride of Southern California. Over the years, my friends would go to In-N-Out and rave about how no other burger compares to the Double-Double. I’ve watched skits overexaggerating their emotions and showing off how delicious the burgers are to them. Popular internet celebrities would fly all the way to Southern California and almost all of them would make a video of them trying (and then annoyingly shouting praises towards the other people eating) the burger. I lived my entire life in Southern California… and I’ve never been to In-N-Out before.</p> 
                            <p>I have been curious about this burger throughout my childhood. In school, if a prize was a free burger from In-N-Out, there would be more participants for the event. Raffle prizes always seem to include In-N-Out food. When people get their blood drawn for donation, the choice was either In-N-Out or a gift card (and most of my friends who did donate blood chose In-N-Out). Once, when I was in the school office waiting for a transcript, another student was ditching class. One of the advisors told her to go to class while the girl was being dismissive. After a minute, the advisor bribed her saying “If you go to class, I’ll buy you In-N-Out” (At this point I was shocked. Getting free food for ditching?). I would be lying if I wasn’t envious at that point.</p> 
                            <p>Fast forward to college and I’m in my dorm with friends. It’s a few minutes before my 18th birthday, and I’m downstairs with my dormmates. My friend Phillip comes back to the dorm with a present for me. I see in his hands it’s an In-N-Out burger: a Double-Double. I thank him and unwrap it. Wow this looks pretty good. The burger was put together pretty neatly and tightly, unlike other establishes like McDonalds where the bun might be sliding halfway across the burger before I open it. The vegetables were fresh-looking, the burger looked perfect, and the onions and sauce glued the burger together. Eagerly, I took a bite of the burger.</p> 
                            <p>It was… decent. I was definitely happy with the burger: the burger was charred nicely, the sauce was savory, and the vegetables were crisp. It was one of the better burgers I’ve had, but I just can’t help but think I’ve been missing out on what everyone else was experiencing. Maybe I don’t like burgers as much as everyone? Maybe my taste buds weren’t sophisticated enough? Maybe the hype was too strong? Regardless, I couldn’t help but think I was lied to or duped. I think to myself maybe I’m being really disrespectful. After hanging out more with my friends and celebrating my birthday with freshman rowdiness, I went to bed around 1pm and reflected on the burger. I wouldn’t say that I crave the burger or even go out of my way to buy it, but I can definitely say that it was a very good burger. Hopefully saying this isn’t sacrilegious and SoCal will hunt me down to apologize.</p> 
                         
                        </div>
                    </Cell>

                    <Cell col={4}>
                        <div className = 'sidebar'>
                            <h1>Archive</h1> 
                            
                         
                        </div>
                    </Cell>
                </Grid>


            </div>
        )
    }
}

export default Food;
