import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';
import "./aboutme.css";

class Food extends Component{
    render() {
        return(
<div style={{width: '100%', margin: 'auto'}}>
                {/*This is how you comment. Has to be in a div*/}
                <Grid className='about-grid'>
                    <Cell col={12}>
                        <div className = 'about-text'>
                            <h1>About Me</h1> 
                         
                        </div>

                    </Cell>
                    <Cell col={12}>
                        <div className = 'about-paragraph'>
                            <p>
                                Hi, my name is Kenneth, and I'm a UCI graduate. I majored in Computer Game Science 
                                with a specialization in Artificial Intelligence. During my I became an officer for osu! UCI, 
                                a club dedicated for the game, osu!, which I have been playing since 2011. I became
                                the president during the Spring quarter of Junior year until my graduation.    
                            </p>
                        </div>
                    </Cell>
                </Grid>

            </div>    

        )
    }
}

export default Food;

