import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';
import "../App.css";

import bg from '../images/marble_bg.jpg';

class Landing extends Component{
    render() {
        return(   

            <div style={{width: '100%', margin: 'auto'}}>
                {/*This is how you comment. Has to be in a div*/}
                <Grid className='landing-grid'>
                    <Cell col={12}>
                        <div className = 'banner-text'>
                            <h1>Hello! My name is Kenneth Yam, and I am a Game Designer and Software Engineer.</h1>
                            <p>C# · Python · C++ · React · HTML/CSS</p>

                            <div className = 'profile-links'>
                                <a href="https://www.linkedin.com/in/kenneth-yam-b89192195/" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-linkedin-square"  aria-hidden="true"/>
                                </a>
                                <a href="https://github.com/kcyam" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-github-square" aria-hidden="true"/>
                                </a>
                                <a href="https://twitter.com/Chhances" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-twitter-square" aria-hidden="true"/>
                                </a>

                            </div>
                        </div>
                    </Cell>
                    <Cell col ={12}>
                        <div className='banner-text'>
                            <p>e-mail: kcyam@uci.edu</p>
                        </div>

                    </Cell>
                </Grid>

            </div>

        )
    }
}
export default Landing;
